// Import 3rd parties libraries
import {React, useEffect, useState} from 'react'
import {CButton, CCollapse, CFormGroup, CLabel} from '@coreui/react'
import L, { geoJSON } from "leaflet";
import ReactSlider from 'react-slider'
import Select from 'react-select'
// Import data untuk diproses
import kotakab from '../../../assets/kotakabcombined.json'
import Daerah from 'src/assets/daerah';
import detailTgl from 'src/assets/detailtgl';

const Dashboard = () =>{
    // Declare beberapa state untuk aplikasi
    const [collapse, setCollapse] = useState(false)
    const [tanggal, setTanggal] = useState('2020-03-01')
    const [legend, setLegend] = useState(null)
    const [map, setMap] = useState(null)
    const [geoJson, setGeoJson] = useState(null) 
    const [informasi, setInformasi] = useState(null)
    const [pallete, setPallete] = useState(0) 
    const [tipedata, setTipeData] = useState('avg_change_of_movement')
    // Declare beberapa konstan seperti array limit untuk change of movement/stay put dan pilihan warna heat map
    const arrBatas = {
      'avg_change_of_movement':[-0.804240, -0.141810, -0.070680, -0.009950, 1.288290],
      'avg_stay_put':[0.071790,0.198090, 0.221310, 0.247170, 0.627540]
    }
    const arrColor = [
      ["#b30000","#e34a33","#fc8d59","#fdcc8a","#000000"],
      ['#006d2c','#2ca25f','#66c2a4','#b2e2e2',"#000000"],
      ['#810f7c','#8856a7','#8c96c6','#b3cde3',"#000000"],
      ['#0868ac','#43a2ca','#7bccc4','#bae4bc',"#000000"],
      ['#7a0177','#c51b8a','#f768a1','#fbb4b9',"#000000"]
    ]    
    // Fungsi untuk mendapatkan warna berdasar skor metrik (change of movement/stay put)
    const getColor = (avgnum,color, tipe) =>{
      var avgmetrik = avgnum == 'na' ? 'na' : parseFloat(avgnum)
      if(avgmetrik == 'na'){
        // Jika Nilai N/A (Tidak diketahui) Return color Black
        return color[4]
      } else if(arrBatas[tipe][1]>avgmetrik){
        return color[0]
      } else if (arrBatas[tipe][2]>avgmetrik){
        return color[1]
      } else if (arrBatas[tipe][3]>avgmetrik){
        return color[2]
      } else {
        return color[3]
      }                  
    }      
    const handleLayer = (tgl, e, tipe) =>{
      var [tempGeo,tempinfo,templegend] = [geoJson,informasi,legend]

      tempinfo.update(false, tgl)
      setInformasi(tempinfo)
      templegend.update(arrColor[e], arrBatas[tipe])
      setLegend(templegend)

      tempGeo.eachLayer((x)=>{    
        const getStyle = () =>{
          var mobility = x.feature.properties.mobility.filter((x)=>x.tgl == tgl)[0]           
          if(mobility){
            const highlightFeature = (e) =>{
              var tempinfo = informasi
              var layer = e.target;
              layer.setStyle({
                  weight: 5,
                  color: '#666',
                  fillOpacity: 0.7
              });
              tempinfo.update(layer.feature.properties, tgl)
              setInformasi(tempinfo)
            }               
            x.on({
                mouseover: highlightFeature
            });            
            x.bindPopup("<b>Kota/Kab : </b>"+ x.feature.properties.NAME_2 + "<br/><b>Change of Movement : </b>"+ mobility['avg_change_of_movement'] + "<br/><b>Stay Put : </b>" + mobility['avg_stay_put'])
            var avgchange = mobility[tipe]                       
          } else {
            x.bindPopup("<b>Kota/Kab : </b>"+ x.feature.properties.NAME_2 + "<br/><b>Change of Movement : </b>N/A<br/><b>Stay Put : </b>N/A")
            var avgchange = "na"            
          }
          return {
                  fillColor: getColor(avgchange, arrColor[e], tipe),
                  fillOpacity: 1,
                  weight: 0.6,
                  opacity: 1,
                  color: 'black',        
          }                  
        }
        x.setStyle(getStyle())
      })
      setGeoJson(tempGeo)
    }        
    const handleChange = (e) =>{
      setTanggal(detailTgl[e].ds)
      handleLayer(detailTgl[e].ds, pallete, tipedata)
    }
    const getKuku = (a, tglawal, tipe) =>{
      var tglselected = detailTgl.filter((x)=>x.ds == tglawal)[0]
      var mobility = a.properties.mobility.filter((x)=>x.tgl == tglawal)[0]
      if(mobility){
        var avgchange = mobility[tipe]        
      } else {
        var avgchange = "na"
      }
      return {
        fillColor:getColor(avgchange,arrColor[pallete], tipe),
        fillOpacity:1,
        color:'black',
        weight: 0.6,
        opacity: 1            
      } 
    }  
    const handleMetrik = (e) =>{
      setTipeData(e.value)
      handleLayer(tanggal, pallete,e.value)
    }  
    const handleZoom = (e) =>{
      var tempGeo = geoJson
      var peta = map
      tempGeo.eachLayer((x)=>{
        if(x.feature.properties.GID_2 == e.value){
          peta.fitBounds(x._bounds)
          x.setStyle({
            weight: 5,
            color: '#666',
            fillOpacity: 0.7
        });          
        }
      })
      setGeoJson(tempGeo)
      setMap(peta)     
    }  
    useEffect(()=>{
        var peta = L.map('map',{
          zoomControl: false
        }).setView([-2.548926, 118.0148634], 5);
        var tile = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        })    
        const highlightFeature = (e) =>{
          // console.log(layer.feature.properties)
          var layer = e.target;
          layer.setStyle({
              weight: 5,
              color: '#666',
              fillOpacity: 0.7
          });
          info.update(layer.feature.properties, tanggal)
        }    
        function resetHighlight(e) {
          var layer = e.target;
          layer.setStyle({
              weight: 0.6,
              color: 'black',
              fillOpacity: 1
          });
        }    
        function zoomToFeature(e) {       
            console.log(e.target.getBounds())
            peta.fitBounds(e.target.getBounds());
        }    
        const onEachFeature = (feature, layer) =>{
          layer.on({
              mouseover: highlightFeature,
              mouseout: resetHighlight,
              click: zoomToFeature
          });
        }               
        var geo = L.geoJSON(kotakab,{
          onEachFeature:(feature, layer)=>onEachFeature(feature, layer,peta,geo),
          style:(feature)=>getKuku(feature, tanggal, tipedata)
        })
        var info = L.control();
        info.onAdd = function (peta) {
            this._div = L.DomUtil.create('div', 'info'); // create a div with a class "info"
            this.update();
            return this._div;
        };
        // method that we will use to update the control based on feature properties passed
        info.update = function (props, tgl) {
            var date = tgl ? tgl : tanggal
            const d = new Date(
              parseInt(date.substring(0,4)),
              parseInt((date.substring(5,7)[0] == '0' ? date.substring(5,7)[1]:date.substring(5,7))),
              parseInt((date.substring(8,11)[0]== '0' ? date.substring(8,11)[1]:date.substring(8,11)))
              )
            const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };              
            this._div.innerHTML = '<h4>'+ d.toLocaleDateString('id', options) + '</h4>' +  (props ?
              '<b>Kota/Kab : </b>'+props.NAME_2+'<br /><b>Change of Movement : </b>' + (
                props.mobility.filter((x)=>x.tgl == tgl)[0] ? 
                props.mobility.filter((x)=>x.tgl == tgl)[0].avg_change_of_movement :
                'N/A'
              ) + '<br/><b>Stay Put: </b>'+ (
                props.mobility.filter((x)=>x.tgl == tgl)[0] ? 
                props.mobility.filter((x)=>x.tgl == tgl)[0].avg_stay_put :
                'N/A'
              )
              : 'Arahkan pointer ke daerah tertentu')
        };  
         
        var legenda = L.control({position: 'bottomright'});
        legenda.onAdd = function () {
            this._div = L.DomUtil.create('div', 'info legend')
            this.update();
            return this._div;
        };   
        legenda.update = function(props, arrLimit){
            // loop through our density intervals and generate a label with a colored square for each interval
            this._div.innerHTML = ""
            if(arrLimit){
              var arr = arrLimit
            } else {
              var arr = arrBatas[tipedata]
            }
            for (var i = 0; i < arr.length; i++) {
              this._div.innerHTML +=
                  '<div class="d-flex flex-row"><i style="background-color:'
                  + 
                  (props ? props[i] : arrColor[pallete][i]) 
                  +
                  '"></i>' 
                  +
                  (
                    arr[i + 1] ? 
                    '['+arr[i] + '] &ndash; [' + arr[i + 1] + ']<br>' 
                    : 
                    'N/A'
                  )
                  +
                  '</div>'
          }          
        }

        legenda.addTo(peta);  
        info.addTo(peta);
        geo.addTo(peta);
        tile.addTo(peta); 

        setLegend(legenda) 
        setInformasi(info)  
        setGeoJson(geo)   
        setMap(peta)           
    },[])
    return(
        <>
        <div className="card d-flex flex-column justify-content-center" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
          <div onClick={()=>setCollapse(!collapse)} role="button" className="card-header d-flex justify-content-between" style={{width:'100%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div><a style={{fontWeight:'500', fontSize:'15px'}}>Control Panel (Click Me)</a></div>
            <div><div onClick={()=>setCollapse(!collapse)} className="btn btn-secondary btn-sm" style={{borderRadius:'12px'}}><i className={collapse ? 'fas fa-chevron-up' : 'fas fa-chevron-down'}></i></div></div>
          </div>
          <CCollapse
          show={collapse}
          >
          <div className="d-flex flex-row mx-3 mb-3 mt-3 justify-content-center">
            <div className="card px-3 py-3 mr-2" style={{marginBottom:'0', width:'50%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
              <div className="d-flex flex-column justify-content-center">
                <div style={{width:'30%'}}><a style={{fontWeight:'300', fontSize:'23px'}}>Linimasa:</a></div>
                <div className="d-flex flex-row justify-content-center">
                  <div className="mr-2 d-flex align-items-center"><a style={{fontWeight:'300',fontSize:'15px'}}><b>01/03/2020</b></a></div>
                  <div className="mr-2 d-flex align-items-center" style={{width:'50%'}}>
                    <ReactSlider
                      min={0}
                      max={detailTgl.length-1}
                      className="horizontal-slider"
                      thumbClassName="example-thumb"
                      trackClassName="example-track"
                      onChange={(e)=>handleChange(e)}
                    />                
                  </div>
                  <div className="d-flex align-items-center"><a style={{fontWeight:'300',fontSize:'15px'}}><b>12/08/2021</b></a></div>
                </div>                   
              </div>            
            </div>
            <div className="card px-3 py-3 mr-2" style={{marginBottom:'0',width:'30%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
              <CFormGroup>
                <CLabel><a style={{fontWeight:'300', fontSize:'23px'}}>Cari Daerah :</a></CLabel>
                <Select
                onChange={(e)=>handleZoom(e)}
                styles={{
                  // Fixes the overlapping problem of the component
                  menu: provided => ({ ...provided, zIndex: 9999 })
                }}                 
                options={Daerah}
                />
              </CFormGroup>
            </div> 
            <div className="card px-3 py-3 mr-2" style={{marginBottom:'0',width:'20%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
              <CFormGroup>
                <CLabel><a style={{fontWeight:'300', fontSize:'23px'}}>Palet Warna :</a></CLabel>
                <Select
                defaultValue={{label:'Pilihan 1', value:'0'}}
                styles={{
                  // Fixes the overlapping problem of the component
                  menu: provided => ({ ...provided, zIndex: 9999 })
                }}               
                onChange={(e)=>{
                  handleLayer(tanggal,parseInt(e.value),tipedata)
                  setPallete(parseInt(e.value))
                }}
                options={[
                  {label:'Pilihan 1', value:'0'},
                  {label:'Pilihan 2', value:'1'},
                  {label:'Pilihan 3', value:'2'},
                  {label:'Pilihan 4', value:'3'},
                  {label:'Pilihan 5', value:'4'},                  
                ]}
                />
              </CFormGroup>
            </div>            
            <div className="card px-3 py-3 mr-2" style={{marginBottom:'0',width:'20%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
              <CFormGroup>
                <CLabel><a style={{fontWeight:'300', fontSize:'23px'}}>Metrik :</a></CLabel>
                <Select
                defaultValue={{label:'Change of Movement', value:'0'}}
                styles={{
                  // Fixes the overlapping problem of the component
                  menu: provided => ({ ...provided, zIndex: 9999 })
                }}               
                onChange={(e)=>handleMetrik(e)}
                options={[
                  {label:'Change of Movement', value:'avg_change_of_movement'},
                  {label:'Stay Put', value:'avg_stay_put'},                 
                ]}
                />
              </CFormGroup>
            </div>             
          </div>            
          </CCollapse>
        </div>
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
          <div id="map" style={{ height: "60vh" }}></div>           
        </div>
        </>
    )
}

export default Dashboard